<template>
  <v-main>
    <v-container>
      <v-card-title class="custom-text-4">List Of Graduates</v-card-title>
      <!-- Tabel Daftar Member -->
      <v-card>
        <v-data-table :headers="headers" :items="members" :items-per-page="10" :search="search" class="elevation-1">
          <template v-slot:item.action="{ item }">
            <v-btn icon @click="viewProfile(item)">
              <v-icon>mdi-eye</v-icon>
            </v-btn>
          </template>
        </v-data-table>
      </v-card>

      <v-dialog v-model="dialog" max-width="800px">
  <v-card>
    <v-card-title class="custom-text-4">Profile Detail</v-card-title>
    <v-divider></v-divider>
    <v-card-text>
      <v-row>
        <v-col cols="12" md="6">
          <div><strong>Name:</strong> {{ selectedMember.name }}</div>
          <div><strong>Birth Place:</strong> {{ selectedMember.birthPlace }}</div>
          <div><strong>Born Date:</strong> {{ selectedMember.birthDay }}</div>
          <div><strong>Gender:</strong> {{ selectedMember.gender }}</div>
          <div><strong>Religion:</strong> {{ selectedMember.religion }}</div>
        </v-col>
        <v-col cols="12" md="6">
          <div><strong>Phone:</strong> {{ selectedMember.phone }}</div>
          <div><strong>Email:</strong> {{ selectedMember.email }}</div>
          <div><strong>Address:</strong> {{ selectedMember.address }}</div>
          <div><strong>Education:</strong> {{ selectedMember.education }}</div>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-divider></v-divider>
          <div><strong>Company Name in Japan:</strong> {{ selectedMember.companyNameJapan }}</div>
          <div><strong>Company Address in Japan:</strong> {{ selectedMember.companyAddressJapan }}</div>
          <div><strong>Date to Japan:</strong> {{ selectedMember.dateToJapan }}</div>
          <div><strong>JLPT Certificate:</strong> {{ selectedMember.jlptCertificate }}</div>
          <div><strong>Skills:</strong> {{ selectedMember.skills }}</div>
          <div><strong>LPK Origin:</strong> {{ selectedMember.LPKOrigin }}</div>
          <div><strong>Date Joined LPK:</strong> {{ selectedMember.DateJoinLPK }}</div>
        </v-col>
      </v-row>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="blue darken-1" text @click="dialog = false">Close</v-btn>
    </v-card-actions>
  </v-card>
</v-dialog>


    </v-container>
  </v-main>
</template>
<script>
import employeeApi from "../../api/EmployeeApi";
import auditrail from "../../api/AuditrailApi";

export default {
  data() {
    return {
      members: [],
      dialog: false,
      selectedMember: {},
      search: '',
      headers: [
        { text: 'Name', value: 'name' },
        { text: 'BirthDay', value: 'birthDay' },
        { text: 'Gender', value: 'gender' },
        { text: 'Religion', value: 'religion' },
        { text: 'JLPT Certificate', value: 'jlptCertificate' },
        { text: 'View Detail', value: 'action', sortable: false },
      ],
    };
  },
  methods: {
    async loadData() {
      try {
        const response = await employeeApi.getList();
        this.members = response;
      } catch (error) {
        console.error("Error loading data:", error);
      }
    },
    viewProfile(member) {
      this.selectedMember = member;
      this.dialog = true;
    },
  },
  async mounted() {
    this.loadData();
    await auditrail.create({
      action: this.$route.path
    });
  }
};
</script>
<style scoped>
.v-footer {
  color: white;
}

.v-card, .v-dialog {
  margin-top: 1cm;
  margin-bottom: 1cm;
}

.v-card-title {
  font-size: 24px;
  font-weight: bold;
}

.v-card-text div {
  padding-bottom: 4px; /* Menambahkan padding di bawah setiap baris */
}
</style>
