        <v-list-item-group active-class="blue--text text--accent-4">
          <v-list-item @click="setLastPage('/lpkmember')">
            <v-list-item-icon>
              <v-icon color="#FFFFFF">mdi-account-details-outline</v-icon>
            </v-list-item-icon>
            <v-list-item-title class="custom-text-sub-drawer">List of Graduates</v-list-item-title>
          </v-list-item>
          <v-list-item @click="setLastPage('/clientlist')">
            <v-list-item-icon>
              <v-icon color="#FFFFFF">mdi-account-details</v-icon>
            </v-list-item-icon>
            <v-list-item-title class="custom-text-sub-drawer">Account List</v-list-item-title>
          </v-list-item>
          <v-list-item @click="setLastPage('/addmember')">
            <v-list-item-icon>
              <v-icon color="#FFFFFF">mdi-account-multiple-outline</v-icon>
            </v-list-item-icon>
            <v-list-item-title class="custom-text-sub-drawer">Add Graduates Data</v-list-item-title>
          </v-list-item>
          <v-list-item @click="setLastPage('/regclient')">
            <v-list-item-icon>
              <v-icon color="#FFFFFF">mdi-account-multiple-plus-outline</v-icon>
            </v-list-item-icon>
            <v-list-item-title class="custom-text-sub-drawer">Create Account</v-list-item-title>
          </v-list-item>
          <v-list-item @click="setLastPage('/auditrailreport')">
            <v-list-item-icon>
              <v-icon color="#FFFFFF">mdi-account-multiple-plus-outline</v-icon>
            </v-list-item-icon>
            <v-list-item-title class="custom-text-sub-drawer">Activity Report</v-list-item-title>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar app color="#222831" dense height="70px">
      <v-app-bar-nav-icon @click="drawer = !drawer" class="white--text text--darken-1"></v-app-bar-nav-icon>

      <v-toolbar-title id="toolbar-title">
        <span class="custom-text-inside white--text text--darken-1">LPK </span>
        <span class="custom-text-inside white--text text--darken-1">AISHIN GROUP</span>
      </v-toolbar-title>

      <!-- Tambahkan spacer agar LOGOUT berada di sisi kanan -->
      <v-spacer></v-spacer>

      <v-menu bottom offset-y>
  <template v-slot:activator="{ on, attrs }">
    <v-btn color="red" depressed rounded small text @click="logout()" v-bind="attrs" v-on="on" class="logout-button">
      <v-icon color="white" class="logout-icon">mdi-logout</v-icon>
      <span class="custom-text-out white--text">LOGOUT</span>
    </v-btn>
  </template>
</v-menu>

    </v-app-bar>


    <v-main>
      <router-view></router-view>
    </v-main>

    <ConfirmationDialog ref="confirmationDialog" />
    <DeleteDialog ref="deleteDialog" />
    <InformationDialog ref="informationDialog" />
    <LoadingDialog ref="loadingDialog" />
    <Snackbar ref="snackbar" />
  </v-app>
</template>

<script>
// import {useRoute} from "vue-router/composables";
import Swal from 'sweetalert2';

export default {
  name: "AppLayout",
  data() {
    return {
      drawer: null,
      identity: null,
    };
  },

  async mounted() {
    // this.identity = this.storageLoad("auth/identity");
    // console.log(this.identity)
    // if(useRoute().path.slice(1) !== "login") {
    //   if (!this.identity) {
    //     await this.$router.push("/login");
    //   }
    // }
    // else{
    //   if (!this.identity) {
    //     await this.$router.push("/login");
    //   }
    //   // else {
    //   //   let lastPage = this.storageLoad("cache/lastPage");
    //   //   console.log(`pindah ke ${lastPage}`)
    //   //   await this.$router.push(lastPage)
    //   // }
    // }
  },

  methods: {
    setLastPage(href) {
      this.storageSave("cache/lastPage", href.replace("/#", ""));
      this.$router.push(href)
    },
    async logout() {
      // Display confirmation with SweetAlert2
      let result = await Swal.fire({
        title: 'Are you sure?',
        text: "You will be logged out of the application.",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Logout',
        cancelButtonText: 'Cancel'
      });

      // If confirmation is clicked, proceed to logout
      if (result.isConfirmed) {
        this.storageRemove("auth/identity");
        // Display success message
        await Swal.fire(
          'Success!',
          'You have been successfully logged out.',
          'success'
        );
        // Redirect to login page
        await this.$router.push("/login");
        // Reload the page to clear data
        this.$router.go(0);
      }
    },

  }
};
</script>
<style>
