<script>
export default {
    name: 'CustomLayout',

    methods: {
        downloadPdf() {
            const path = '/images/brosur.pdf'; // 

            const link = document.createElement('a');
            link.href = path;
            link.setAttribute('download', 'brosur.pdf'); // 
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        }
    }
}
