<template>
  <v-main>

    <v-container>
      <v-card-title class="custom-text-4">Account List</v-card-title>
      <!-- Kartu Pencarian -->
      <v-card class="mb-5">
        <v-card-title>
          Pencarian Klien
        </v-card-title>
        <v-card-text>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Cari Klien..."
            single-line
            hide-details
          ></v-text-field>
        </v-card-text>
      </v-card>

      <!-- Tabel Daftar Klien -->
      <v-card>
        <v-card-title>
          Daftar Klien
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="filteredClients"
          :search="search"
          class="elevation-1"
          :loading="loading"
          loading-text="Memuat... Harap tunggu"
          dense
        >
          <template v-slot:item.actions="{ item }">
            <v-icon
              small
              class="mr-2"
              @click="editItem(item)"
            >
              mdi-pencil
            </v-icon>
            <v-icon
              small
              @click="deleteItem(item)"
            >
              mdi-delete
            </v-icon>
          </template>
        </v-data-table>
      </v-card>
    </v-container>
  </v-main>
</template>


<script>
import userApi from "../../api/UserApi";
import auditrail from "../../api/AuditrailApi";

export default {
  data() {
    return {
      search: '',
      loading: false,
      clients: [],
      headers: [
        { text: 'Username', value: 'username' },
        { text: 'Nama Perusahaan', value: 'companyName' },
        { text: 'Alamat Perusahaan', value: 'companyAddress' },
        { text: 'Nomor Telepon', value: 'phoneNumber' },
        { text: 'Nama Kontak Utama', value: 'contactPerson' },
        { text: 'Email Kontak Utama', value: 'contactEmail' },
        { text: 'Tanggal Mulai Kerjasama', value: 'startDate' },
        { text: 'Tanggal Berakhir Kerjasama', value: 'endDate' },
        { text: 'User Role', value: 'userRole' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
    };
  },
  computed: {
    filteredClients() {
      return this.clients.filter(client => {
        return Object.values(client).some(value => 
          value.toString().toLowerCase().includes(this.search.toLowerCase())
        );
      });
    }
  },
  methods: {
    async loadClients() {
      this.loading = true;
    try {
      const response = await userApi.getList();
      console.log("Data received:", response); 
      this.clients = response;
    } catch (error) {
      console.error("Terjadi kesalahan saat memuat data: ", error);
      this.$refs.snackbar.showError("Gagal memuat data: " + error.message);
    } finally {
      this.loading = true;
    }
    
  },
    editItem(item) {
      console.log("Edit item: ", item);
    },
    deleteItem(item) {
      console.log("Delete item: ", item);
    },
  },
  async mounted() {
    this.loadClients();
    await auditrail.create({
      action: this.$route.path
    });
  }
};
</script>

<style scoped>
.v-footer {
  color: white;
}

.v-card {
  margin-top: 1cm;
  margin-bottom: 1cm;
}
</style>
